body {
    background: #E0E0E0;
}

.PostWrapper {
    width: 50rem;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .PostWrapper {
        width: 90%;    
    }
}
.Header {
    background-color: #00B5AD;
    color: white;
    font-size: 2rem;
    padding: 1.2rem;
    text-align: center;
    font-weight: 600;
    margin-bottom: 2rem;
}

.EventPost {
  padding: 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;

  text-align: center;
}

.EventPost h1 {
  font-size: 2rem;
}

.EventPost p {
  font-size: 2rem;   
}

.EventPost .Sponsor {
  color:#888;
}

.EventPost .ImgBox {
  background-color: rgba(0, 0, 0, 0.05);
  text-align: center;
  padding:20px;
}

.EventPost .ItemImage {
  width:200px;
  height:200px;
}

.EventPost .SponsorIcon {
  width:48px;
  height:48px;
}

.EventPost .CrossIcon {
  width:48px;
  height:48px;
  opacity: 0.5;
}
.SimpleLoader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgb(0, 0, 0);
  opacity: 0.2;
}

.App {
  text-align: center;
  padding: 40px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
